<template lang="pug">
.comp-car-filter-price-pop
    van-popup.popup-wrap(v-model="visible" position="right")
        .list
            .item
                .title 首付
                .sub-items
                    .sub-item 1万以内
                    .sub-item 1-2万
                    .sub-item 2-3万
                    .sub-item 3-4万
                    .sub-item 4万以上
            .item
                .title 月供
                .sub-items
                    .sub-item 1千以内
                    .sub-item.choosed 1-2千
                    .sub-item 2-3千
                    .sub-item 3-4千
                    .sub-item 4千以上
            .item
                .title 指导价
                .sub-items
                    .sub-item 5万以下
                    .sub-item 5-8万
                    .sub-item 8-12万
                    .sub-item 12-18万
                    .sub-item 18-25万
                    .sub-item 25-40万
                    .sub-item 40万以上
        my-bottom-bar.btns.flex
            .left(@click="visible=false") 取消
            .right 查看
</template>

<script>
export default {
    data () {
        return {
            visible: false
        };
    },
    methods: {
        open () {
            this.visible = true;
        }
    }
};
</script>

</<style lang="scss" scoped>
.comp-car-filter-price-pop {
    width: 100%;
    height: 100%;
    .popup-wrap {
        width: 100%;
        height: 100%;
        .btns {
            .left, .right {
                width:50%;
                text-align:center;
                line-height: 50px;
                color:#fff;
            }
            .left {
                background-color:#f5f5f5;
                color:#999;
            }
        }
        .list {
            height: calc(100% - 50px);
            overflow: auto;
            .title {
                padding: 1rem;
                color: #333;
            }
            .sub-items {
                padding: 0 1rem 0;
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                .sub-item {
                    padding: 0.6rem 1rem;
                    background-color: #f1f1f1;
                    color: #999;
                    flex-shrink: 0;
                    margin: 0 1rem 1rem 0;
                    &.choosed {
                        color: #fff;
                        background-color: #e86352;
                    }
                }
            }

        }
    }
}
</style>



