<template lang="pug">
.page-car
    my-header
    car-filter
    .car-filter-2
        .item.cur i 尊车
        .item i 品车
        .item i 享车
        .item i 优车
        .item i 值车
    car-list.list(type="row")
</template>

<script>
import CarFilter from '@/components/CarFilter';
import CarList from '@/components/CarList';
export default {
    components: {
        'car-filter': CarFilter,
        'car-list': CarList
    }
};
</script>

<style lang="scss" scoped>
.page-car {
    .car-filter-2 {
        display: flex;
        align-items: center;
        border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
        .item {
            width: 20%;
            text-align: center;
            font-size: 0.87rem;
            padding: 0.5rem 0;
            border-right: 1px solid #eee;
            &:last-child {
                border-right: 0 none;
            }
            &.cur {
                background-color: #f04c03;
                color: #fff;
            }
        }
    }
    .list {
        margin-top: 1rem;
    }
}
</style>



