<template lang="pug">
.comp-car-filter-brand-pop
    van-popup.popup-wrap(v-model="visible" position="right")
        .list
            .item(v-for="j in 10")
                .title A
                .sub-items
                    .sub-item(v-for="(i, index) in 5" :key="index")
                        van-checkbox 宝马
        my-bottom-bar.btns.flex
            .left(@click="visible=false") 取消
            .right 查看
</template>

<script>
export default {
    data () {
        return {
            visible: false
        };
    },
    methods: {
        open () {
            this.visible = true;
        }
    }
};
</script>

</<style lang="scss" scoped>
.comp-car-filter-brand-pop {
    width: 100%;
    height: 100%;
    .popup-wrap {
        width: 100%;
        height: 100%;
        .btns {
            .left, .right {
                width:50%;
                text-align:center;
                line-height: 50px;
                color:#fff;
            }
            .left {
                background-color:#f5f5f5;
                color:#999;
            }
        }
        .list {
            height: calc(100% - 50px);
            overflow: auto;
            .title {
                padding: 0.5rem 1rem;
                background: #f5f5f5;
                color: #999;
            }
            .sub-items {
                padding: 0.3rem 0;
            }
            .sub-item {
                padding: 0.3rem 1rem;
            }
        }
    }
}
</style>



