<template lang="pug">
.comp-car-filter-other-pop
    van-popup.popup-wrap(v-model="visible" position="right")
        .list
            .item
                .title 排量
                .sub-items
                    .sub-item 1.0以下
                    .sub-item 1.0-2.0
                    .sub-item 2.0-3.0
                    .sub-item 3.0-4.0
                    .sub-item 4.0以上
            .item
                .title 变速箱
                .sub-items
                    .sub-item 手动
                    .sub-item.choosed 自动
            .item
                .title 进气形式
                .sub-items
                    .sub-item 自然吸气
                    .sub-item 涡轮增压
            .item
                .title 能源
                .sub-items
                    .sub-item 汽油
                    .sub-item 油电混合
        my-bottom-bar.btns.flex
            .left(@click="visible=false") 取消
            .right 查看
</template>

<script>
export default {
    data () {
        return {
            visible: false
        };
    },
    methods: {
        open () {
            this.visible = true;
        }
    }
};
</script>

</<style lang="scss" scoped>
.comp-car-filter-other-pop {
    width: 100%;
    height: 100%;
    .popup-wrap {
        width: 100%;
        height: 100%;
        .btns {
            .left, .right {
                width:50%;
                text-align:center;
                line-height: 50px;
                color:#fff;
            }
            .left {
                background-color:#f5f5f5;
                color:#999;
            }
        }
        .list {
            height: calc(100% - 50px);
            overflow: auto;
            .title {
                padding: 1rem;
                color: #333;
            }
            .sub-items {
                padding: 0 1rem 0;
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                .sub-item {
                    padding: 0.6rem 1rem;
                    background-color: #f1f1f1;
                    color: #999;
                    flex-shrink: 0;
                    margin: 0 1rem 1rem 0;
                    &.choosed {
                        color: #fff;
                        background-color: #e86352;
                    }
                }
            }

        }
    }
}
</style>



