<template lang="pug">
.comp-car-filter-sort-pop
    van-popup(v-model="visible" position="bottom")
        van-picker(:columns="columns")
</template>

<script>
export default {
    data () {
        return {
            visible: false,
            columns: [
                '智能排序',
                '首付由低到高',
                '首付由高到低',
                '月供由低到高',
                '月供由高到低',
                '车价由低到高',
                '车价由高到低'
            ]
        };
    },
    methods: {
        open () {
            this.visible = true;
        }
    }
};
</script>



